import {html, fixture, expect} from '@open-wc/testing';

import {PixelBox} from './pixel-box';
import '../../../dist/Components/Box/pixel-box';

describe('Component', () => {
  it('should renders child correctly', async () => {
    const child = 'This is a child box';
    const el: PixelBox = await fixture(html` <pixel-box>${child}</pixel-box> `);
    expect(
      el.shadowRoot?.querySelector('slot')?.assignedNodes()[0].textContent
    ).to.contain(child);
  });
});
